.home {
  margin: auto;
  margin-top: 99px;
  .hPLeftSection {
    max-width: 22%;
    padding-bottom: 0px;
    -webkit-align-self: flex-start;
    -ms-flex-item-align: start;
    align-self: flex-start;
    border-right: 1px none rgba(0, 0, 0, 0.07);
    background-color: hsla(0, 0%, 61%, 0.11);
    box-shadow: none;
    -webkit-transform: scale(1.15);
    -ms-transform: scale(1.15);
    transform: scale(1.15);
    .hPLeftSection1 {
      p {
        padding: 0 31px;
      }
    }
    div {
      padding-top: 19px;
      padding-bottom: 19px;
      p {
        position: relative;
        box-shadow: none;
        color: rgba(51, 51, 51, 0.62);
        font-weight: 500;
        margin-bottom: 0;
        font-size: 16px;
        display: flex;
        padding: 11px 31px;
        -webkit-box-align: center;
        font-family: "Sf pro text", sans-serif;
      }
      #address {
        color: #000;
        font-size: 16px;
        font-weight: 400;
        margin-bottom: 0;
      }
      #balance {
        margin-top: 10px;
        color: rgba(0, 0, 0, 0.47);
        line-height: 15px;
        font-weight: 400;
        font-size: 14px;
      }
      #coin1 {
        position: relative;
        background-color: hsla(0, 3%, 64%, 0.09);
        box-shadow: none;
        color: #000;
        font-weight: 500;
        display: flex;
        -webkit-box-align: center;
      }
    }
    .hPLeftSection2,
    .hPLeftSection3 {
      p {
        &:hover {
          background-color: rgba(0, 0, 0, 0.09);
          -webkit-box-pack: justify;
          position: relative;
          box-shadow: none;
          font-weight: 500;
          -webkit-box-align: center;
        }
      }
    }
    .hPLeftSection3 {
      #orders {
        span {
          right: 0;
          position: absolute;
          margin-right: 30px;
          background: red;
          border-radius: 99px;
          padding-right: 6px;
          padding-left: 6px;
          color: white;
          font-size: 12px;
          line-height: 20px;
          font-weight: 300;
        }
      }
    }
  }
  .hPRightSection {
    padding-left: 7%;
    margin-top: -30px;
    .hPRightTopSection {
      button {
        letter-spacing: 1.5px;
        float: left;
      }
      #buybu {
        width: auto;
        margin-right: 0px;
        padding-right: 32px;
        padding-left: 32px;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        -ms-flex-pack: center;
        justify-content: center;
        border-style: none;
        border-color: #004279;
        border-bottom-width: 4px;
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
        background-color: #0070e0;
        color: #fff;
        height: 42.8px;
        font-weight: 600;
        font-size: 12px;
        font-family: "MySoBold";
      }
      #sellbu {
        font-family: "MySoBold";
        width: auto;
        font-weight: 600;
        font-size: 12px;
        margin-right: 0px;
        padding-right: 32px;
        padding-left: 32px;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        -ms-flex-pack: center;
        justify-content: center;
        border-style: none;
        border-color: #692aed #692aed hsla(0, 0%, 65%, 0.49);
        border-bottom-width: 4px;
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
        background-color: hsla(0, 0%, 65%, 0.23);
        color: #2e2e2e;
        height: 42.8px;
      }
    }
    .hPRightBottomSection {
      padding-top: 41px;
      .coinTable {
        // width: 100%;
        .head {
          border-style: none none solid;
          border-width: 1px;
          border-color: transparent transparent #e1e8ed;
          box-shadow: none;
          div {
            p {
              text-align: left;
              padding: 0 0px;
              color: #070707;
              font-size: 16px;
              font-family: "MySoRegular";
            }
          }
        }
        .coinRow {
          padding-top: 15px;
          padding-bottom: 15px;
          border-style: none;
          &:hover {
            background-image: linear-gradient(180deg, hsla(156, 2%, 55%, 0.07), hsla(156, 2%, 55%, 0.07));
          }

          div {
            display: -webkit-inline-box;
            img {
              margin-right: 7px;
            }
            p {
              font-size: 15px;
              font-weight: 400;
              justify-content: space-between;
              color: #333;
              font-family: "MySoRegular";
              text-align: center;
            }
            a {
              border: 1px solid #e1e8ed;
              background-color: #fff;
              box-shadow: 0 1px 1px 0 hsla(0, 0%, 73%, 0.21);
              color: #0d0d0e;
              margin-top: -8px;
              margin-bottom: -8px;
              margin-left: 40%;
              padding: 7px 23px;
              border-radius: 3px;
              font-size: 12px;
              font-weight: 600;
              letter-spacing: 1px;
              text-transform: uppercase;
              font-family: "MySoBold";
            }
            .valueDolar {
              color: rgba(51, 51, 51, 0.62);
            }
          }
        }
        .active {
          background-image: linear-gradient(180deg, hsla(156, 2%, 55%, 0.07), hsla(156, 2%, 55%, 0.07));
        }
      }
    }
  }
  .popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.3);
    display: none;
    z-index: 999;
  }
  .popup_innerCompare {
    position: absolute;
    left: 25%;
    right: 25%;
    top: 25%;
    bottom: 25%;
    margin: auto;
    background: white;
    width: 900px;
    height: 561px;
    border-radius: 5px;
    .xbutton {
      width: 24px;
      height: 24px;
      float: right;
      margin-right: 25px;
      margin-top: 25px;
      cursor: pointer;
    }
    h4 {
      padding: 30px 0px 0px 35px;
      color: #000;
      font-size: 21px;
      font-weight: 700;
      text-align: left;
      font-family: "MySoRegular";
      text-decoration: none;
    }
    .BarChart {
      width: 100%;
      .leftCol {
        width: 15%;
        height: 100%;
        bottom: 0;
        margin-left: 12px;
        p {
          color: #333;
          font-size: 15px;
          line-height: 20px;
          font-family: "MySoRegular";
          span {
            width: 79%;
            border-bottom: 1px solid lightgray;
            position: absolute;
            margin-top: 10px;
            margin-left: 25px;
            z-index: 0;
          }
        }
        p:nth-child(1) {
          padding-top: 68px;
        }
        p:nth-child(2) {
          padding-top: 134px;
        }
        p:nth-child(3) {
          padding-top: 134px;
          margin-bottom: 0;
        }
      }
      .positionOnCol {
        height: 100%;
        width: 80%;
        bottom: 0;
        display: table-cell;
        vertical-align: bottom;
        z-index: 99;
        .rightCol {
          display: inline-flex;
          bottom: 0;
          width: 100%;
          padding-top: 360px;
          height: 500px;
          .bar {
            bottom: 0;
            width: 30px;
            margin-right: 68px;
            margin-bottom: 82px;
            background-color: #0084f0;
            box-shadow: inset 0 -22px 0 0 hsla(206.76258992805754, 0%, 100%, 0.33);
          }
          div:nth-child(8) {
            margin-right: 0;
          }
        }
      }
      .pogore {
        margin-left: -5px;
        margin-top: -45px;
      }
    }
    .iconBar {
      display: inline-block;
      padding-top: 0 !important;
      height: 30px !important;
      margin-top: -40px;
      margin-left: 156px;
      img {
        width: 30px;
        height: 30px;
        bottom: 0;
        margin-right: 68px;
        margin-bottom: -3px;
      }
    }
  }
  .popup_innerLimit2 {
    display: none;
    width: 788px;
    height: 559px !important;
    h4 {
      padding: 0px 51px 10px 35px !important;
      line-height: 35px;
      font-size: 21;
    }
    .paddingRow {
      #pContinue {
        font-size: 14px;
      }
      #pContinueWhite {
        color: #999;
        font-weight: 300;
        font-size: 14px;
      }
      .padCol {
        padding: 0;
        div {
          float: left;
        }
        p {
          color: #0084f0;
          font-size: 14px !important;
          padding-top: 3px;
          font-family: "MySoRegular";
          strong {
            color: #0084f0;
          }
        }
        #textWhite {
          color: #333 !important;
        }
      }
      .checkDiv {
        width: 30px;
        height: 30px;
        margin-right: 11px;
        padding: 4px;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        -ms-flex-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        -webkit-align-items: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
        flex: 0 0 auto;
        border-style: solid;
        .check {
          width: 15px;
          height: 15px;
          max-width: 100%;
          // vertical-align: middle;
          display: block !important;
          margin: auto;
        }
        .check2 {
          display: none !important;
        }
      }
    }
  }
  .popup_innerLimit,
  .popup_innerLimit2 {
    position: absolute;
    left: 25%;
    right: 25%;
    top: 25%;
    bottom: 25%;
    margin: auto;
    background: white;
    width: 788px;
    height: 650px;
    border-radius: 5px;
    .leftColo {
      border-right: 1px solid hsla(0, 1%, 60%, 0.32);
      height: 580px;
      margin-top: 40px;
      width: 105px !important;
      padding-left: 5px;
      cursor: pointer;
      .row {
        margin: auto;

        h2 {
          width: 100%;
          text-align: center;
          margin: auto;
          color: #000;
          font-size: 21px;
          line-height: 21px;
          font-weight: 700;
          margin-left: -5px;
        }
        p {
          width: 100%;
          text-align: center;
          padding-left: 12px;
          margin-bottom: 0;
          color: #000;
          font-size: 16px;
        }
        .step {
          padding-bottom: 16px;
          -webkit-box-orient: vertical;
          -webkit-box-direction: normal;
          width: 30px;
          margin-left: 40px;
          margin-bottom: 1em;
          border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        }
        .stepBlue {
          box-shadow: inset 0 -5px 0 0 #0084f0;
        }
      }
    }
    img {
      width: 30px;
      height: 30px;
      float: right;
      margin-right: 20px;
      margin-top: 25px;
    }
    h4 {
      padding: 0px 0px 10px 35px;
      color: #000;
      font-size: 22px;
      font-weight: 700;
      text-align: left;
      font-family: "MySoRegular";
      text-decoration: none;
    }
    .rightColo {
      padding: 0;
      .col-12 {
        padding-right: 0;
      }
      .tradeParag {
        display: inline-flex;
        margin-top: 33px;
        margin-bottom: 27px;
      }
      .coinParag {
        border: 1px solid #dadcdf;
        border-radius: 7px;
        background-color: #fff;
        display: inline-flex;
        width: 270px;
        padding-top: 11px;
        padding-bottom: 11px;
        padding-left: 16px;
        .divleft {
          width: 50%;
          margin: auto;
          p {
            margin-bottom: 0;
            margin-bottom: 1px;
            color: rgba(0, 0, 0, 0.61);
            font-size: 11px;
            text-align: left;
          }
          #biggerNum {
            margin-bottom: 6px;
            color: #000;
            font-size: 16px;
            line-height: 15px;
          }
        }
        .imgright {
          display: inline-flex;
          width: 50%;
          padding: 0px 13px 0px 11px;
          border-left: 1px solid rgba(0, 0, 0, 0.21);
          p {
            margin: auto;
            font-size: 13px;
            font-family: "MySoRegular";
            font-weight: 400;
            color: black;
          }
          i {
            margin: auto;
            font-size: 12px;
          }
          img {
            margin-right: 0;
            margin-top: 0;
            margin: auto;
            width: 28px;
            height: 28px;
          }
        }
      }
      #tradeimgP {
        margin-right: 10px;
        margin-left: 10px;
        .tradeimg {
          position: relative;
          width: 37px;
          height: 37px;
          margin-top: 12px;
          margin-bottom: 12px;
          padding: 4px;
          border: 1px solid #dadcdf;
          border-radius: 99px;
          background-color: #fff;
          margin-right: 0;
        }
      }
      .divider {
        border-top: 1px solid rgba(0, 0, 0, 0.13);
        margin: 10px 45px;
        padding-top: 30px;
        div {
          padding: 0;
        }
        #dividerLeft {
          text-align: left;
          font-size: 17px;
          justify-content: space-between;
        }
        #dividerRight {
          display: flex;
          -webkit-box-align: center;
          -webkit-align-items: center;
          -ms-flex-align: center;
          align-items: center;
          padding-left: 197px;
          margin-top: -15px;
          img {
            margin: 0;
            display: inline-block;
            margin-left: 16px;
            padding: 5px;
            align-items: center;
            border: 1px solid #e1e8ed;
            border-radius: 99px;
            box-shadow: none;
            width: 35px;
            height: 35px;
          }
        }
      }
      .paddingRow,
      .formGroup {
        margin: 10px 45px;
        margin-bottom: 0;
        p {
          font-size: 17px;
          margin-bottom: 0;
        }
      }
      .formGroup {
        form {
          width: 100%;
          margin-bottom: 15px;
          input {
            padding: 26px 16px;
            border-style: solid;
            border-color: #dfdfdf;
            border-radius: 7px;
            background-color: #fff;
            color: #000;
            font-size: 16px;
            font-weight: 300;
            display: inline-flex;
            width: 100%;
            height: 55px;
          }
          #continueButton {
            cursor: pointer;
            min-width: 150px;
            margin-top: 80px !important;
            padding: 13px 20px 12px 20px;
            -webkit-box-pack: justify;
            -webkit-justify-content: space-between;
            -ms-flex-pack: justify;
            justify-content: space-between;
            -webkit-box-align: center;
            -webkit-align-items: center;
            -ms-flex-align: center;
            align-items: center;
            background-color: #0070e0;
            box-shadow: none;
            color: #fff;
            border-radius: 89px;
            font-size: 12px;
            font-weight: 500;
            letter-spacing: 1px;
            font-family: "MySoBold";
            text-transform: uppercase;
            text-align: left;
            float: right;
            img {
              width: 24px;
              height: 24px;
              margin: 0;
              margin-left: 20px;
              margin-top: -4px;
              display: inline-block;
            }
          }
        }
      }
    }
  }
  .popup_innerC {
    position: absolute;
    left: 25%;
    right: 25%;
    top: 25%;
    bottom: 25%;
    margin: auto;
    background: white;
    width: 900px;
    height: 710px;
    border-radius: 5px;
    img {
      width: 24px;
      height: 24px;
      float: right;
      margin-right: 25px;
      margin-top: 25px;
      cursor: pointer;
    }
    h4 {
      padding: 30px 0px 10px 35px;
      color: #000;
      font-size: 22px;
      font-weight: 700;
      text-align: left;
      font-family: "MySoRegular";
      text-decoration: none;
    }
    p {
      width: 100%;
      color: #333;
      font-size: 16px;
      line-height: 20px;
      text-align: left;
      padding: 30px 67px 10px 34px;
    }
    ul {
      padding-left: 80px;
      li {
        width: 50%;
        margin-bottom: 10px;
        p {
          text-align: left;
          padding: 0;
          line-height: 20px;
          margin-bottom: 0;
          color: #333;
          font-size: 15px;
          font-family: "MySoRegular";
        }
        .liMid {
          margin-top: 5px;
          margin-bottom: 5px;
          color: #999;
        }
        .liBot {
          padding-bottom: 0px;
          border-bottom: 1px none hsla(0, 3%, 60%, 0.27);
          color: #0084f0;
          font-weight: 300;
          text-decoration: none;
          width: 100%;
          height: 12px;
          font-family: "MySoRegular";
        }
      }
    }
  }
  .popup_inner2 {
    display: none;
    position: absolute;
    left: 25%;
    right: 25%;
    top: 25%;
    bottom: 25%;
    margin: auto;
    background: white;
    width: 460px;
    height: 400px !important;
    border-radius: 5px;
    div {
      display: inline-flex;
      width: 90%;
      #right {
        text-align: right;
        padding-left: 25px;
        span {
          padding-bottom: 0px;
          border-bottom: 1px none hsla(0, 3%, 60%, 0.27);
          color: #0084f0;
          font-weight: 300;
          text-decoration: none;
        }
      }
    }
    button {
      width: 75%;
      margin-top: 20px !important;
      text-align: left;
    }
  }
  .popup_inner,
  .popup_inner2 {
    position: absolute;
    left: 25%;
    right: 25%;
    top: 25%;
    bottom: 25%;
    margin: auto;
    background: white;
    width: 460px;
    height: 560px;
    border-radius: 5px;
    img {
      width: 24px;
      height: 24px;
      float: right;
      margin-right: 25px;
      margin-top: 25px;
      cursor: pointer;
    }
    h4 {
      padding-top: 65px;
      width: 70%;
      margin-left: 15%;
      margin-bottom: 24px;
      color: #000;
      font-size: 21px;
      line-height: 35px;
      font-weight: 700;
      text-align: center;
      font-family: "MySoRegular";
    }
    p {
      padding-top: 15px;
      font-size: 16px;
      text-align: left;
      margin-left: 10%;
      color: #333;
      font-family: "MySoRegular";
    }
    input {
      display: block;
      padding: 24px 18px;
      border-style: solid;
      border-radius: 7px;
      background-color: #fff;
      color: #000;
      font-size: 14px;
      font-weight: 300;
      border-width: 1px;
      border-color: hsla(0, 0%, 51%, 0.27);
      width: 80%;
      height: 38px;
      margin-left: 10%;
    }
    button {
      min-width: 150px;
      margin-top: 45px;
      padding: 13px 20px 12px 20px;
      -webkit-box-pack: justify;
      -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
      justify-content: space-between;
      -webkit-box-align: center;
      -webkit-align-items: center;
      -ms-flex-align: center;
      align-items: center;
      background-color: #0070e0;
      box-shadow: none;
      color: #fff;
      border-radius: 89px;
      font-size: 12px;
