@media only screen and (max-width: 1440px) {
  .home {
    .popup {
      .popup_innerC {
        left: 20%;
      }
      .popup_innerCompare {
        left: 21%;
      }
    }
  }
}
@media only screen and (max-width: 1280px) {
  .home {
    .popup {
      .popup_innerLimit,
      .popup_innerLimit2 {
        left: 20%;
      }
      .popup_innerC {
        left: 15%;
      }
      .popup_innerCompare {
        left: 16%;
      }
    }
  }
}
@media only screen and (max-width: 1024px) {
  .home {
    .hPRightSection .hPRightBottomSection .coinTable .coinRow div a {
      display: none;
    }
    .popup {
      .popup_innerLimit,
      .popup_innerLimit2 {
        left: 7%;
      }
      .popup_inner,
      .popup_inner2 {
        left: 20%;
      }
      .popup_innerC {
        left: 2%;
      }
      .popup_innerCompare {
        left: 1%;
        width: 89%;
      }
    }
  }
}
@media only screen and (max-width: 800px) {
  .home {
    .hPLeftSection {
      padding-left: 22px;
      max-width: 25%;
    }
    .popup {
      .popup_innerLimit,
      .popup_innerLimit2 {
        left: 3%;
        width: 85%;
        .tradeParag {
          // display: block;
          width: 85%;
        }
        .rightColo {
          .divider #dividerRight {
            padding-left: 135px;
          }
          .formGroup form #continueButton {
            margin-top: 40px !important;
          }
        }
      }
      .popup_inner,
      .popup_inner2 {
        left: 16%;
      }
      .popup_innerC {
        width: 87%;
        ul {
          padding-left: 30px;
        }
      }
      .popup_innerCompare {
        .BarChart .leftCol p span {
          margin-left: 2%;
        }
        .BarChart .positionOnCol .rightCol .bar {
          margin-right: 40px;
        }
        .iconBar {
          margin-left: 128px;
          img {
            margin-right: 40px;
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 580px) {
  .home {
    margin-top: 9%;
    .hPRightSection {
      margin-top: 10%;
      width: 100%;
      padding: 0;
      .hPRightTopSection {
        padding-bottom: 4%;
        button {
          width: 50% !important;
          font-size: 10px;
        }
      }
      .hPRightBottomSection {
        display: none;
      }
    }
    .hPLeftSection {
      padding-left: 22px;
      max-width: 100%;
      margin-left: 8%;
    }
    .popup {
      .popup_innerLimit,
      .popup_innerLimit2 {
        overflow-y: auto;
        img {
          margin-right: 15%;
        }
        .leftColo {
          width: 70px !important;
          .row {
            .step {
              margin-left: 35%;
            }
          }
        }
        .rightColo {
          flex: auto;
          width: 0%;
